.btn-toggle{

    top :-4px;
    right: 100px;
    border-radius: 50%;
    width: 75px;
    height: 35px;
    text-decoration: none;
    display: inline-block;
    border: none;
    text-align: center;
    border: none;
    transition-duration: 0.4s;
    font-weight: bold;
    border-radius: 12px;
    cursor: pointer;
}

.dark-btn{
    background: #222;
    color: #f1f1f1;
}

.light-mode{
    color :yellow;
    height: 30px;
    width: 30px;
}

.dark-mode{
    color :#f1f1f1;
    height: 20px;
    width: 20px;
}


