.home{
   
    margin: 100px auto 0;
    padding-left: 25%;
    font-size: 29px;
    pointer-events: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    
}

a{
    text-decoration: none;
}

.paragraphes{
    max-width: 1050px;
	display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    text-align: justify;
    text-align-last: center;
    font-size: 22px;
    margin-left: 300px;

}

p {
    line-height: 2em;
    margin: 1em 0;
    text-align: justify;
  }



  .GitHubLogo3{
    width: 230px;
    height: 129px;
    cursor: pointer;
    margin-left: 22%;
    text-decoration: none;
    
}

a, a:hover, a:focus, a:active {
    text-decoration: none;
    color: inherit;
}



 
@media (max-width:480px)  {

.GitHubLogo3{
    width: 153px;
    height: 86px;
    cursor: pointer;
    margin-left: 13%;

}

.home{
   
    margin: 100px auto 0;
    padding-left: 2%;
    font-size: 20px;
    pointer-events: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    
}

.paragraphes{

        max-width: 450px;
        margin: 22px auto 0;
        padding-left: -12%;
        height: auto;
        font-size: 15px;
        font-weight: normal;
    
}

p {
    line-height: 1.2em;
    
   
  }

}



