.missions{
    
    margin: 100px auto 0;
    padding-left: 69%;
    height: auto;
    font-size: 29px;
}



  .App-logo {
    height: 30vmin;
    
    pointer-events: none;
  }
  
  @media (prefers-reduced-motion: no-preference) {
    .App-logo {
      animation: App-logo-spin infinite 20s linear;
    }
  }
  
  .App-header {
   
    min-height: 50vh;
    padding-left: 60%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
   
  }
  
 
  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  
.missions2{
  width: 900px;
  font-size: 22px;
  text-align: center;
  justify-content: center;
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-left: 250px;
}



.aws,.truck{
    width: 200px;
    height: 200px;      
    float: left;
    padding: 5px;
    margin-right: 150px;
}

.terraform,.computer{

  width: 200px;
  height: 200px;      
  float: left;
  padding: 15px;
  margin-right: -100px;
}






@media (max-width:480px)  { 

  .missions{
    
    margin: 100px auto 0;
    padding-left: 40%;
    height: auto;
    font-size: 20px;

}

.App-header {
   
  min-height: 50vh;
  padding-left: 15%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  
}



.missions2{

  max-width: 450px;
  margin: 100px auto 0;
  padding-left: -20%;
  margin-left: -14%;
  height: auto;
  margin-top: 4%;
  font-size: 15px;

}



.aws,.truck{
        
        width: 100px;
        height: 100px;
        margin-left: -15%;
        height: auto;

}

.terraform,.computer{
        
  width: 100px;
  height: 100px;
  margin-left: -30%;
  height: auto;
}

}